import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, ModalController } from '@ionic/angular';
import { MessageService } from 'primeng/api';
import { RegraNewDTO } from 'src/app/models/regra/regra-new.dto';
import { MesasService } from 'src/app/services/mesas/mesas.service';
import { RegraService } from 'src/app/services/regra/regra.service';
import { StorageService } from 'src/app/services/storage-service.service';

@Component({
  selector: 'app-regra-new',
  templateUrl: './regra-new.component.html',
  styleUrls: ['./regra-new.component.scss'],
})
export class RegraNewComponent implements OnInit {

  loading = false;

  formRegra: FormGroup;

  constructor(public modalCtrl: ModalController, private mesaService: MesasService,
    public formBuilder: FormBuilder, public alertController: AlertController,
    public storage: StorageService, private toast: MessageService) {

    this.formRegra = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
    });
  }

  ngOnInit() { }

  addRegra() {
    this.loading = true;

    const regra: RegraNewDTO = {
      nome: this.formRegra.get('nome').value,
      descricao: this.formRegra.get('descricao').value
    }

    const idMesa = this.mesaService.getMesaSelecionada();
    console.log('Mesa ID:', idMesa)

    this.mesaService.addRegraNew(idMesa, regra)
      .subscribe(res => {
        console.log(res);
        this.loading = false;
        this.callToast('success', 'Adicionado!', 'Regra adicionada com sucesso!');
        this.modalCtrl.dismiss(res.sistema.sistemas);
      },
        error => {
          this.loading = false;
        });
  }

  callToast(severity, summary, detail) {
    this.toast.clear();
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  close(){
    this.modalCtrl.dismiss();
  }

}
