import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ModalController } from '@ionic/angular';

import { MessageService } from 'primeng/api';

import { MesasService } from 'src/app/services/mesas/mesas.service';
import { DetalhaNpcComponent } from '../detalha-npc/detalha-npc.component';
import { FichaService } from 'src/app/services/fichas/ficha.service';
import { DetalhaComponenteComponent } from '../detalha-componente/detalha-componente.component';

import { PaginacaoComponent } from '../../utils/paginacao/paginacao.component'
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { CapituloService } from 'src/app/services/historia/capitulo.service';
import { FaccoesService } from 'src/app/services/criacoes/faccoes.service';
import { NpcService } from 'src/app/services/npc/npc.service';
import { MapaService } from 'src/app/services/mapa/mapa.service';
import { Subscription } from 'rxjs';
import { RegraNewComponent } from '../regra-new/regra-new.component';

@Component({
  selector: 'app-gerencia-componente',
  templateUrl: './gerencia-componente.component.html',
  styleUrls: ['./gerencia-componente.component.scss'],
})
export class GerenciaComponenteComponent implements OnInit {

  @Input('template')
  template = {
    nome: '',
    codigo: '',
    tabelaCodigo: '',
    tabelaNome: '',
    tipo: '',
  }

  @Input('idMesa')
  idMesa = '';

  @Input('promisse')
  promisse;

  @Input('atributo')
  atributo;

  @Input('id')
  idJson: any = '';

  @Input('nome')
  nomeJson: any = '';

  @Input('abas')
  abas: boolean = false;

  @Output('atributoMudado')
  atributoMudado: EventEmitter<any> = new EventEmitter();
  handleMudanca() {
    this.atributoMudado.emit(this.abaAtual);
  }

  @Output('modal')
  modal: EventEmitter<any> = new EventEmitter();
  modalClose() {
    this.modal.emit('closed');
  }

  paginaAtual = 0;


  itens = [];
  idObj: string = '';
  idObjRemovido: string = '';
  rowSelected: HTMLElement;
  loading: boolean = true;
  selectedItem;
  objs = [];
  abaAtual = 'bom';
  tipoObj;
  idFichaModelo;

  subscription: Subscription;

  constructor(private mesaService: MesasService, private modalCtrl: ModalController,
    private fichaService: FichaService, private paginaService: PaginacaoService, private capituloService: CapituloService,
    private faccaoService: FaccoesService, private npcService: NpcService, private mapaService: MapaService,
    private toast: MessageService) { }




  ngOnInit() {

    this.subscription = this.paginaService.getRefresh().subscribe(message => {
      if (message >= 0) {

        this.paginaAtual = message;
        const current = this.paginaService.getCurrentTab();

        this.nextPage(current);
      }
    });

    this.rowSelected = document.createElement("b");
    this.promisse.subscribe(res => {
      this.atualizaItens(res);
      this.loadContent();
      this.loading = false;
    },
      error => {
        this.loading = false;
      });
  }

  ionViewDidLeave() {
    this.paginaService.setConfiguration(false);
    this.paginaService.closeBTN = false;
    this.paginaService.isModal = false;
  }

  ngOnDestroy() {
    this.paginaService.setConfiguration(false);
    this.paginaService.closeBTN = false;
    this.paginaService.isModal = false;
    this.paginaService.setInternalLoad(false);
    this.subscription.unsubscribe();
  }

  atualizaItens(res) {
    this.itens = undefined;
    if (res.sistema) {
      this.itens = res.sistema[this.atributo];
    }

    if (!this.itens) {
      this.itens = res[this.atributo];
    }

    if (!this.itens) {
      this.itens = res;
    }

    if (res.idFichaModelo) {
      this.idFichaModelo = res.idFichaModelo;
    }

  }

  loadContent() {
    this.objs = [];

    for (let i = 0; i < this.itens.length; i++) {
      this.objs.push({
        id: this.getProperty(this.itens[i], this.idJson),
        nome: this.getProperty(this.itens[i], this.nomeJson)
      });
    }
  }

  getProperty(json, field) {

    if (json == null || field == null) {
      return null;
    }

    var value = json;
    var fields = field.split(".");
    for (var i = 0; i < fields.length; i++) {

      value = value[fields[i]];

      if (value == null) {
        return null;
      }
    }

    return value;
  }

  addItem() {
    this.loading = true;
    const operation = this.getSelectedService(this.template.tipo, 'adiciona');
    console.log(operation);
    operation(this.idObj, this.idMesa, this.tipoObj)
      .subscribe(res => {
        this.atualizaItens(res);
        this.loadContent();
        this.loading = false;
      },
        error => {
          this.loading = false;
        });
    this.idObj = '';
  }

  removItem() {
    this.loading = true;
    const operation = this.getSelectedService(this.template.tipo, 'remocao');

    if (operation == this.mesaService.removRegraNew) {
      this.mesaService.removRegraNew(this.idObjRemovido, this.mesaService.getMesaSelecionada())
        .subscribe(res => {
          this.atualizaItens(res);
          this.loadContent();
          this.loading = false;
        },
          error => {
            this.loading = false;
          });
    } else {
      operation(this.idObjRemovido, this.idMesa, this.tipoObj)
        .subscribe(res => {
          this.atualizaItens(res);
          this.loadContent();
          this.loading = false;
        },
          error => {
            this.loading = false;
          });
    }

  }

  selecionar(item, row: HTMLElement) {
    this.rowSelected.classList.remove('selected');
    row.classList.add('selected');
    this.rowSelected = row;
    this.selectedItem = item;
    this.idObjRemovido = item.id;
  }

  getTipoNpcAdd() {
    switch (this.abaAtual) {
      case 'bom':
        this.tipoObj = 0;
        break;

      case 'neutro':
        this.tipoObj = 1;
        break;

      case 'mau':
        this.tipoObj = 2;
        break;
    }
    return this.mesaService.addNpc;
  }

  getTipoNpcRemov() {
    switch (this.abaAtual) {
      case 'bom':
        this.tipoObj = 0;
        break;

      case 'neutro':
        this.tipoObj = 1;
        break;

      case 'mau':
        this.tipoObj = 2;
        break;
    }
    return this.mesaService.removNpc;
  }

  getTipoFaccaoAdd() {
    switch (this.abaAtual) {
      case 'bom':
        this.tipoObj = 0;
        break;

      case 'neutro':
        this.tipoObj = 1;
        break;

      case 'mau':
        this.tipoObj = 2;
        break;
    }
    return this.mesaService.addFaccao;
  }

  getTipoFaccaoRemov() {
    switch (this.abaAtual) {
      case 'bom':
        this.tipoObj = 0;
        break;

      case 'neutro':
        this.tipoObj = 1;
        break;

      case 'mau':
        this.tipoObj = 2;
        break;
    }
    return this.mesaService.removFaccao;
  }

  getSelectedService(tipo, opcao) {
    if (opcao == 'remocao') {
      switch (tipo) {
        case 'jogador':
          return this.mesaService.removJogador;
          break;

        case 'regra':
          return this.mesaService.removRegraNew;
          break;

        case 'npc':
          return this.getTipoNpcRemov();
          break;

        case 'faccao':
          return this.getTipoFaccaoRemov();
          break;

        case 'capitulo':
          return this.mesaService.removCapitulos;
          break;

        case 'mapa':
          return this.mesaService.removMapa;
          break;
      }
    } else {
      switch (tipo) {
        case 'jogador':
          this.mesaService.setObjAuxiliar({
            idFichaModelo: this.idFichaModelo,
            idJogador: this.idObj,
            valorInventario: 0,
            idMesa: this.idMesa
          });
          return this.mesaService.addJogador;
          break;

        case 'regra':
          return this.mesaService.addRegra;
          break;

        case 'npc':
          return this.getTipoNpcAdd();
          break;

        case 'faccao':
          return this.getTipoFaccaoAdd();
          break;

        case 'capitulo':
          return this.mesaService.addCapitulos;
          break;

        case 'mapa':
          return this.mesaService.addMapa;
          break;
      }
    }

  }

  getColor() {
    switch (this.abaAtual) {
      case 'bom':
        return 'info';
        break;

      case 'neutro':
        return 'warning';
        break;

      case 'mau':
        return 'danger';
        break;
    }
  }

  mudaAba(event) {
    this.handleMudanca();
    this.promisse.subscribe(res => {
      this.atualizaItens(res);
      this.loadContent();
      this.loading = false;
    },
      error => {
        this.loading = false;
      });
    this.loadContent();
  }

  view(event, item) {
    this.presentModal(item);
  }

  async presentModal(item) {
    this.getTipoNpcAdd();
    let component: any = DetalhaComponenteComponent;

    switch (this.template.tipo) {
      case 'npc':
        const npc = this.itens.find(element => element.idNpc == item.id);
        this.mesaService.setObjAuxiliar({
          idJogador: item.id,
          idMesa: this.idMesa,
          npc: npc
        });

        //console.log(this.mesaService.getObjAuxiliar());
        const { ficha } = this.itens.find(element => element.idNpc == item.id);
        this.fichaService.setConfiguration(ficha, false, item.id, this.idMesa, this.tipoObj, true);
        component = DetalhaNpcComponent;

        break;

      case 'faccao':
        this.mesaService.setObjAuxiliar({
          idFaccao: item.id,
          idMesa: this.idMesa,
          tipo: this.tipoObj
        });
        break;

      case 'jogador':
        this.fichaService.setConfiguration(undefined, true, undefined, this.idMesa, undefined, true);
        this.mesaService.setObjAuxiliar({
          idJogador: item.id,
          idMesa: this.idMesa,
        });

        break;

      case 'capitulo':
        this.mesaService.setObjAuxiliar({
          idCapitulo: item.id,
          idMesa: this.idMesa,
        });
        break;

      case 'mapa':
        this.mesaService.setObjAuxiliar({
          idMapa: item.id,
          idMesa: this.idMesa,
        });
        break;

      case 'regra':
        this.mesaService.setObjAuxiliar({
          idRegra: item.id
        });
        break;

    }
    const modal = await this.modalCtrl.create({
      component,
      backdropDismiss: true,
      cssClass: 'my-custom-modal-css',
      id: 'comp'
    });

    modal.onWillDismiss()
      .then(data => {
        this.modalClose();
      });

    return await modal.present();
  }

  async listItem(wantModal?) {
    // console.log('ENTROU')
    let p;
    let f;

    switch (this.template.tipo) {

      case 'jogador':
        this.callToast('warn', 'Indisponível', 'Listagem de jogadores não disponível ainda');
        return;
        break;

      case 'regra':
        //this.callToast('error', 'Indisponível', 'Regras devem ser criadas individualmente, não é possível reutilizar regras');
        // return;
        this.regraModal();
        return;
        break;

      case 'npc':
        p = this.npcService.findPageByCriador(this.paginaAtual.toString());
        f = this.npcService.findPageByCriador;
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idNpc', 'ficha.fichaModelo.nome', 'historia',
          'ficha.fichaModelo.tipoRPG', 'ficha.urlImg', null, 'Não foi encontrado nenhum item', true, this.paginaAtual, true, true);
        this.paginaService.setCurrentTab('npc');
        break;

      case 'faccao':
        p = this.faccaoService.findPageByCriador(this.paginaAtual.toString());
        f = this.faccaoService.findPageByCriador
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idFaccao', 'nome', 'descricao',
          'objetivo', 'imgURL', null, 'Não foi encontrado nenhum item', true, this.paginaAtual, true, true);
        this.paginaService.setCurrentTab('faccao');
        break;

      case 'capitulo':
        p = this.capituloService.findPageByCriador(this.paginaAtual.toString());
        f = this.capituloService.findPageByCriador
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idCapitulo', 'titulo', 'historia',
          'numCapitulo', 'imgURL', null, 'Não foi encontrado nenhum item', true, this.paginaAtual, true, true);
        this.paginaService.setCurrentTab('capitulo');
        break;

      case 'mapa':
        p = this.mapaService.findPageByCriador(this.paginaAtual.toString());
        f = this.mapaService.findPageByCriador
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idMapa', 'nome', 'descricao',
          'objetivo', 'urlMapa', null, 'Não foi encontrado nenhum item', true, this.paginaAtual, true, true);
        this.paginaService.setCurrentTab('mapa');
        break;
    }

    this.paginaService.setFunctionPromissed(f);
    this.paginaService.setInternalLoad(true);


    if (wantModal == true) {
      const modal = await this.modalCtrl.create({
        component: PaginacaoComponent,
        backdropDismiss: false,
        cssClass: 'my-custom-modal-css',
      });

      modal.onDidDismiss()
        .then(res => {
          // console.log(res);
          if (res.data) {
            this.idObj = res.data.id;
            this.addItem();
          }

        });

      return await modal.present();
    }

  }

  nextPage(current: string) {
    let p;

    switch (current) {

      case 'jogador':
        this.callToast('warn', 'Indisponível', 'Listagem de jogadores não disponível ainda');
        return;
        break;

      case 'regra':
        this.callToast('error', 'Indisponível', 'Regras devem ser criadas individualmente, não é possível reutilizar regras');
        return;
        break;

      case 'npc':
        p = this.npcService.findPageByCriador(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idNpc', 'ficha.fichaModelo.nome', 'historia',
          'ficha.fichaModelo.tipoRPG', 'ficha.urlImg', null, 'Não foi encontrado nenhum item', false, this.paginaAtual, true, true);
        break;

      case 'faccao':
        p = this.faccaoService.findPageByCriador(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idFaccao', 'nome', 'descricao',
          'objetivo', null, null, 'Não foi encontrado nenhum item', false, this.paginaAtual, true, true);
        break;

      case 'capitulo':
        p = this.capituloService.findPageByCriador(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idCapitulo', 'titulo', 'historia',
          'numCapitulo', null, null, 'Não foi encontrado nenhum item', false, this.paginaAtual, true, true);
        break;

      case 'mapa':
        p = this.mapaService.findPageByCriador(this.paginaAtual.toString());
        this.paginaService.setPromisse(p);
        this.paginaService.setConfiguration(true, 'idMapa', 'nome', 'descricao',
          'objetivo', 'urlMapa', null, 'Não foi encontrado nenhum item', false, this.paginaAtual, true, true);
        break;
    }

    this.paginaService.setInternalLoad(true);
  }

  callToast(severity, summary, detail) {
    this.toast.add({
      severity: severity,
      summary: summary,
      detail: detail
    });
  }

  async regraModal() {

    this.mesaService.setStaticID(this.idMesa);

    const modal = await this.modalCtrl.create({
      component: RegraNewComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
    });

    modal.onDidDismiss()
      .then(res => {
        console.log(res);
        if (res.data) {
          this.itens = res.data;
          this.loadContent();
        }

      });

    return await modal.present();
  }

}