import { Component, OnInit } from '@angular/core';
import { MesasService } from 'src/app/services/mesas/mesas.service';
import { MesaInicialDTO } from 'src/app/models/Mesa/mesa-inicial.dto';
import { UserService } from 'src/app/services/user/user.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-mesa-inicial',
  templateUrl: './mesa-inicial.component.html',
  styleUrls: ['./mesa-inicial.component.scss'],
})
export class MesaInicialComponent implements OnInit {

  mesa: MesaInicialDTO = {
    descricaoRpg: '',
    historiaRpg: '',
    idMesa: '',
    idRpg: '',
    jogadores: [],
    mestre: {
      email: '',
      id: '',
      nome: ''
    },
    nomeRpg: '',
    idFichaModelo: ''
  };

  loading: boolean = true;
  jogadores: [] = [];
  rowSelected: HTMLElement;
  abaAtual: string = 'info';
  atributoSelecionado = 'aliados';
  selectedJogador;
  template = {
    nome: 'Jogadores',
    codigo: 'Código do Jogador',
    tabelaCodigo: 'Código',
    tabelaNome: 'Apelido',
    tipo: 'jogador',
  };
  promisse;

  constructor(private mesaService: MesasService,
    private userService: UserService, private router: Router) { }

  ngOnInit() {
    this.abaAtual = 'info';
    this.rowSelected = document.createElement("b");
    this.setPromisse();
  }

  setPromisse() {
    this.promisse = this.mesaService.findMesaInicial();
    this.mesaService.findMesaInicial()
      .subscribe(res => {
        this.mesa = res;
        this.jogadores = res.jogadores;
        this.loading = false;
      },
        error => {
          this.loading = false;
          this.abaAtual = 'regras';
          this.router.navigateByUrl('perfil');
        });
  }

  selecionar(jogador, row: HTMLElement) {
    this.rowSelected.classList.remove('selected');
    row.classList.add('selected');
    this.rowSelected = row;
    this.selectedJogador = jogador;
  }

  getColor(aba) {
    if (aba = this.abaAtual) {
      return 'success';
    } else {
      return "warning";
    }
  }

  changeTemplate() {
    this.promisse = this.mesaService.findMesa(this.mesa.idMesa);
    switch (this.abaAtual) {
      case 'info':
        this.template = {
          nome: 'Jogadores',
          codigo: 'Código do Jogador',
          tabelaCodigo: 'Código',
          tabelaNome: 'Apelido',
          tipo: 'jogador',
        }
        this.promisse = this.mesaService.findMesaInicial();
        break;

      case 'regras':
        this.template = {
          nome: 'Regras',
          codigo: 'Código da Regra',
          tabelaCodigo: 'Código',
          tabelaNome: 'Nome',
          tipo: 'regra',
        }

        break;

      case 'npcs':
        this.template = {
          nome: 'NPCs',
          codigo: 'Código do NPC',
          tabelaCodigo: 'Código',
          tabelaNome: 'Nome',
          tipo: 'npc',
        }
        break;

      case 'faccoes':
        this.template = {
          nome: 'Facções',
          codigo: 'Código da Facção',
          tabelaCodigo: 'Código',
          tabelaNome: 'Nome',
          tipo: 'faccao',
        }
        break;

      case 'capitulo':
        this.template = {
          nome: 'Capítulos',
          codigo: 'Código do capítulo',
          tabelaCodigo: 'Código',
          tabelaNome: 'Nome',
          tipo: 'capitulo',
        }
        break;

      case 'mapa':
        this.template = {
          nome: 'Mapas',
          codigo: 'Código do mapa',
          tabelaCodigo: 'Código',
          tabelaNome: 'Nome',
          tipo: 'mapa',
        }
        break;
    }
  }

  mudaAba(event) {
   // this.setPromisse();
   this.promisse = this.mesaService.findMesaInicial();
    if (this.abaAtual == 'npcs') {
      this.atributoSelecionado = 'aliados';
    } else {
      this.atributoSelecionado = 'faccoesBoas';
    }
    this.changeTemplate();
    this.mesaService.setComponentAtual(this.abaAtual)
  }

  mudaAtributo(event) {
    if (this.abaAtual == 'npcs') {
      switch (event) {
        case 'bom':
          this.atributoSelecionado = 'aliados';
          break;

        case 'neutro':
          this.atributoSelecionado = 'neutros';
          break;

        case 'mau':
          this.atributoSelecionado = 'inimigos';
          break;

        default:
          this.atributoSelecionado = 'aliados';
          break;
      }
    } else {
      switch (event) {
        case 'bom':
          this.atributoSelecionado = 'faccoesBoas';
          break;

        case 'neutro':
          this.atributoSelecionado = 'faccoesNeutras';
          break;

        case 'mau':
          this.atributoSelecionado = 'faccoesHostis';
          break;

        default:
          this.atributoSelecionado = 'faccoesBoas';
          break;
      }
    }


  }

  async modalClosed(evt) {
    this.abaAtual = 'info';
  }

}
